import RestofySource from '../../data/restofy-source';
import UrlParser from '../../routes/url-parser';

const Search = {
  async render() {
    return `
      <section class="restaurant">
        <div id="restaurant-list">
          <p>Loading...</p>
        </div>
      </section>
    `;
  },

  async afterRender() {
    const url = UrlParser.parseActiveUrlWithoutCombiner();
    const restaurantContainer = document.querySelector('.restaurant');
    const restaurantListContainer = document.querySelector('#restaurant-list');

    const { error, founded, restaurants } =
      await RestofySource.searchRestaurant(url.id);

    if (founded > 0 && !error) {
      restaurantListContainer.innerHTML = '';
      restaurants.forEach((restaurant) => {
        const restaurantItem = document.createElement('restaurant-item');
        restaurantItem.restaurant = restaurant;

        restaurantListContainer.appendChild(restaurantItem);
      });
    } else {
      restaurantContainer.innerHTML = `
        <p class='message'>No restaurant found</p>
      `;
    }
  },
};

export default Search;
